.cart-dropdown {
    position: absolute;
    width: 300px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 60px;
    right: 10px;
    z-index: 5;
  
    .cart-items {
      height: 240px;
      display: flex;
      flex-direction: column;
      overflow: auto;

      .empty-message {
        font-size: 18px;
        margin: auto;
      }
    }
  
    button {
      margin-top: auto;
    }
  }
  